@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);

.Terminal {
  min-height: 100px;
  padding: 12px;
  border: 2px solid #3A6990;
}

.Terminal .CommandLine {
  margin: 0;
  font-weight: normal;
  font-size: 24px;
  color: white;
}

.Prompt {
  color: #28AF07;
}
body {
  background-color: #0C2732;
  margin: 16px;
  font-family: 'Courier Prime', monospace;
  color: white;
}

.Terminal {
  margin-bottom: 16px;
}

pre {
  font-size: 16px;
  text-align: center;
}

.ListHeader {
  font-weight: normal;
  margin-bottom: 8px;
}

.List {
  padding-left: 16px;
  margin: 0 0 8px 0;
}


